<template>
  <div>
    <div class="mt-4">
      <form class="form-signin" @submit.prevent="signin">
        <div class="text-center py-4">
          <img :src="`${baseUrl}/img/logo.svg`" alt="" width="80">
        </div>
        <!-- <h1 class="h3 mb-3 font-weight-normal">登入</h1> -->
        <label for="inputEmail" class="sr-only">Email address</label>
        <input type="email" id="inputEmail" class="form-control" v-model="user.username" placeholder="Email address" required autofocus>

        <label for="inputPassword" class="sr-only">Password</label>
        <input type="password" id="inputPassword" class="form-control" v-model="user.password" placeholder="Password" required>

        <div class="checkbox mb-3">
          <label><input type="checkbox" value="remember-me" class="mr-1">記住我</label>
        </div>
        <button class="btn btn-block btn-dark" type="submit">登入</button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
      return {
          baseUrl: process.env.VUE_APP_IMGURL,
          user: {
            username: '',
            password: ''
          }
      }
  },
  methods: {
      signin() {
        const api = `${process.env.VUE_APP_APIPATH}/admin/signin`;
        const vm = this;
        this.$http.post(api, vm.user).then((res) => {
            // console.log(res.data)
            if(res.data.success) {
                vm.$router.push('/admin/products');
            }
        })
      }
  }
};
</script>

<style scoped>
html,
body {
  height: 100%;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #f5f5f5;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>
